import {
	baseUrl
} from '@/api/api.js'
import {
	getToken
} from '@/utils/auth.js'
const install = (Vue, vm) => {
	Vue.prototype.$u.http.setConfig({
		baseUrl: 'https://api.yuxicloud.cn/api',
		loadingText: '~',
		loadingTime: 8000,
		// trueresponseresponse.data
		// truethis.$u.http.interceptor.response
		// originalData: true, 
		// content-type
		// header: {
		// 	'content-type': 'xxx'
		// }
	});
	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		config.header.Authorization = `Bearer ` + getToken()
		// if(config.url == '/user/login') config.header.noToken = true;
		// vuextokenuViewvuexhttps://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;

		// uViewvuex$store.state
		// config.header.token = vm.$store.state.token;

		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;

		// tokenStorageStorage
		// const token = uni.getStorageSync('token');
		// config.header.token = token;

		return config;
	}
	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		// originalDatatrue
		// res.statueCoderes.data.code
		// if(res.code == 200) {
		// 	// originalDatatruereturnthis.$u.postthen
		// 	return res.data;  
		// } else return false;

		if (res.status_code >= 200 && res.status_code < 400) {
			// rescoderesult
			// res.resultthis.$u.post(url).then(res => {})thenres
			// originalDatatrue
			return res;
		} else if (res.status_code >= 400 && res.status_code < 500) {
			// 201token
			vm.$u.toast(res.info);
			// setTimeout(() => {
			// 	// uView
			// 	vm.$u.route('/pages/user/login')
			// }, 1500)
			return false;
		} else {
			// falsePromisereject
			// this.$u.post(url).then().catch(res=>{})catchres
			return false;
		}
	}
}

export default {
	install
}
